// Validation des heures silencieuses
function isInQuietHours(currentTime, quietHours) {
    if (!quietHours.enabled)
        return false;
    const current = currentTime.getHours() * 60 + currentTime.getMinutes();
    const [startHour, startMin] = quietHours.startTime.split(':').map(Number);
    const [endHour, endMin] = quietHours.endTime.split(':').map(Number);
    const start = Number(startHour) * 60 + Number(startMin);
    const end = Number(endHour) * 60 + Number(endMin);
    // Grer le cas o les heures silencieuses traversent minuit
    if (start > end) {
        return current >= start || current <= end;
    }
    return current >= start && current <= end;
}
// Validation du contenu des templates
function validateTemplateContent(content, requiredVariables) {
    const usedVariables = content.match(/\{([^}]+)\}/g)?.map(v => v.slice(1, -1)) || [];
    const missingVariables = requiredVariables.filter(req => !usedVariables.includes(req));
    return {
        isValid: missingVariables.length === 0,
        missingVariables
    };
}
// Validation de la limite de notifications par utilisateur
function validateNotificationRateLimit(userId, type, recentNotifications, limits) {
    const now = new Date();
    const oneHourAgo = new Date(now.getTime() - 60 * 60 * 1000);
    const oneDayAgo = new Date(now.getTime() - 24 * 60 * 60 * 1000);
    const hourlyCount = recentNotifications.filter(n => n.type === type && n.createdAt >= oneHourAgo).length;
    const dailyCount = recentNotifications.filter(n => n.type === type && n.createdAt >= oneDayAgo).length;
    if (hourlyCount >= limits.perHour) {
        return {
            allowed: false,
            reason: 'Limite horaire dpasse',
            retryAfter: 3600 - Math.floor((now.getTime() - oneHourAgo.getTime()) / 1000)
        };
    }
    if (dailyCount >= limits.perDay) {
        return {
            allowed: false,
            reason: 'Limite quotidienne dpasse',
            retryAfter: 86400 - Math.floor((now.getTime() - oneDayAgo.getTime()) / 1000)
        };
    }
    return { allowed: true };
}

