class StringUtils {
    static capitalize(str) {
        return str.charAt(0).toUpperCase() + str.slice(1).toLowerCase();
    }
    static camelCase(str) {
        return str
            .replace(/(?:^\w|[A-Z]|\b\w)/g, (word, index) => index === 0 ? word.toLowerCase() : word.toUpperCase())
            .replace(/\s+/g, '');
    }
    static kebabCase(str) {
        return str
            .replace(/([a-z])([A-Z])/g, '$1-$2')
            .replace(/[\s_]+/g, '-')
            .toLowerCase();
    }
    static truncate(str, length, suffix = '...') {
        if (str.length <= length)
            return str;
        return str.substring(0, length - suffix.length) + suffix;
    }
    static slugify(str) {
        return str
            .toLowerCase()
            .trim()
            .replace(/[^\w\s-]/g, '')
            .replace(/[\s_-]+/g, '-')
            .replace(/^-+|-+$/g, '');
    }
    static generateRandomString(length, includeNumbers = true) {
        const letters = 'abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ';
        const numbers = '0123456789';
        const chars = includeNumbers ? letters + numbers : letters;
        let result = '';
        for (let i = 0; i < length; i++) {
            result += chars.charAt(Math.floor(Math.random() * chars.length));
        }
        return result;
    }
    static isValidEmail(email) {
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        return emailRegex.test(email);
    }
    static isValidPhoneNumber(phone) {
        const phoneRegex = /^\+?[\d\s\-\(\)]{10,}$/;
        return phoneRegex.test(phone);
    }
    static maskEmail(email) {
        const [username, domain] = email.split('@');
        if (!username || !domain || username.length <= 2)
            return email;
        const masked = username.charAt(0) + '*'.repeat(username.length - 2) + username.charAt(username.length - 1);
        return `${masked}@${domain}`;
    }
    static maskPhone(phone) {
        const cleaned = phone.replace(/\D/g, '');
        if (cleaned.length < 4)
            return phone;
        const visible = 4;
        const masked = '*'.repeat(cleaned.length - visible) + cleaned.slice(-visible);
        return masked;
    }
}
